import React, { Component } from 'react';
import CartItem from './CartItem';
import './Cart.css';

import { client } from '../../App';

class Cart extends Component {
  constructor(props) {
    super(props);

    this.state = {
      items: []
    }

    this.init();
  }

  init() {
    client.get("/cart/items")
      .then(resp => {
        console.log("Resp: " + JSON.stringify(resp));

        this.setState({
          items: resp.data
        });
      })
      .catch(err => {
        console.log("Something went wrong: " + JSON.stringify(err));
      });
  }
  
  renderItem(item) {
    return (
    <CartItem 
      id={item.productId}
      key={item.productId}
      name={item.name}
      category={item.category}
      count={item.count}
    />
    )
  };

  addItem(id) {
    client.post("/cart/add", {shelfItemId: id})
      .then(resp => {
        console.log("Resp: " + JSON.stringify(resp));

        this.setState({
          items: resp.data
        });
      })
      .catch(err => {
        console.log("Something went wrong: " + JSON.stringify(err));
      });
  }

  render() {
    return (
      <div className="Cart">
        <h1>Cart</h1>
        {this.state.items.map(itm => this.renderItem(itm))}
      </div>
    );
  }
}

export function addItem(id) {
  return this.addItem(id);
};

export default Cart;
