import React from 'react';
import './ShelfItem.css';

function ShelfItem(props) {
  return (
    <div className="ShelfItem">
      <div>
        <div className="ShelfItem-thumb">
          <img src={require(`../../../static/${props.category}.png`)} alt="logo" className={props.color} />
        </div>
      </div>
      <div>
        <div className="ShelfItem-description">
          {props.description}
        </div>
      </div>
      <div>
        <button className="ShelfItem-add-btn" onClick={() => props.onClick()}>
          Add to cart
        </button>
      </div>
    </div>
  );
}

export default ShelfItem;